/* Import CSS reset and base styles */
/* @import "global.css"; */

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

:root {
    --color-bg: #fff;
    --color-bg-2: #f9fafa;
    --color-text: #1f203d;
	--color-text-blockquote: #7d7d7d;
    --color-bg-pre: #020205;
    --color-text-pre: #dedede;
    --color-blue-ac-dark:#161229;
    --color-blue-ac-lighten:#4b3e8e;
    --color-blue-ac: #1fa5c7;
    --color-mint-ac: #45D6CE;
    --color-red-ac:#d81927;
    --color-line: #d6dcdc;
    --padding-viewport: 6vw;
    --padding-viewport-small: 6vw;
    --max-width-container: 1070px;
    --max-width-container-extended: 1200px;
    --hero-cards-height: 30px;
    --hero-cards-height-tablet-desktop: 165px;
    --font-family-heading: 'Montserrat', sans-serif;
    --font-family: 'Source Sans Pro', sans-serif;
    --spacing-unit: 2.5rem;
}

html {
    font-size: 62.5%;
    font-family: var(--font-family);
    line-height: 1.7rem;
    background-color: var(--color-bg);
}
body {
    color: var(--color-text);
    padding: 0;
    margin: 0;
    font-size: 1.8rem;
    line-height: 2.0em;

    font-style: normal;
    letter-spacing: 0;

    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: "liga" on;
}
a {
    color: var(--color-blue-ac);
    text-decoration: none;
}
a:active,
a:hover {
    outline: 0;
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

fieldset {
    margin: 0;
    padding: 0;
    border: 0;
}

textarea {
    resize: vertical;
}

ol,
ul {
    padding-left: 1.3em;
    padding-right: 1.5em;
}

ol ol,
ul ul,
ul ol,
ol ul {
    margin: 0.5em 0 1em;
}

ul {
    list-style: disc;
}

ol {
    list-style: decimal;
}

ul,
ol {
    max-width: 100%;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

li {
    padding-left: 0.3em;
    line-height: 1.6em;
}

li + li {
    margin-top: 0.5em;
}

dt {
    float: left;
    margin: 0 20px 0 0;
    width: 120px;
    color: #daf2fd;
    font-weight: 500;
    text-align: right;
}

dd {
    margin: 0 0 5px 0;
    text-align: left;
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-family-heading);
    line-height: 1.3em;
    margin-bottom: 1.3rem;
    font-weight: 600;
    letter-spacing: -0.01em;
    font-stretch: expanded;
    transform: scaleY(1.03);
    text-rendering: optimizeLegibility;
}

h1 {
    font-size:2.8rem;
}
h2 {
    font-size:2.4rem; 
}
h3 {
    font-size:2rem; 
}

img {
    border: 0;
}


pre {
    background-color: var(--color-bg-pre);
    color: var(--color-text-pre);
    padding: 16px;
    overflow: auto;
    border-radius: 6px;
    line-height: 1.2em;
    font-size: 2.1rem;
    margin: 2rem 0;
}

pre code {
    background: transparent;
    padding: 0px;
}

code {
    padding: 3px 6px;
    margin: 0;
    font-size: 1.7rem;
    background-color: #d9d9d9;
    border-radius: 6px;
}


p.poweredby{
    font-family: var(--font-family-heading);
    font-size: 1.4rem;
    line-height: 1rem;
    font-weight: 600;
}

.mt-1 {
    margin-top: var(--spacing-unit);
}
.mt-2 {
    margin-top: calc(var(--spacing-unit) * 2);
}

.main {
    min-height: 90vh;
}

article {
    color: var(--color-text);
}

article p {
    line-height: 2em;
}

article blockquote, article dl, article ol, article p, article pre, article ul {
    margin: 1.2em 0;
}

article h1, article h2, article h3, article h4, article h5, article h6 {
    margin: 1.75em 0 1.1em;
    line-height: 1.33;
}

article blockquote {
	color: var(--color-text-blockquote);
    border-left: 3px solid;
    padding-left: 1.4em;
    padding-right: calc(1.4em + 3px);
}


.hero-section {
    display: flex;
    position: relative;
    justify-content: flex-end;
    width: 100%;
    min-height: 380px;
    flex-direction: column;
    box-sizing: border-box;
    padding-left: var(--padding-viewport);
    padding-right: var(--padding-viewport);
    background-color: var(--color-bg-2);
    color: var(--color-text);
    z-index:2;
}

.hero-section-with-bg {
    color: #fff;
    text-shadow: 1px 1px 8px rgba(0, 0, 0, 0.34);
}

.hero-section__background {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    overflow: hidden;
}

.hero-section__button {
    margin: 1.3rem 0 3.9rem;
    z-index: 4;
}
.carr-mobile-controls__container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    overflow: hidden;
    z-index: 3;
}

.carr-mobile-controls__back, .carr-mobile-controls__next {
    position: absolute;
    height: inherit;
    width: 20%;
    min-width: 90px;
    background-color: transparent;
}

.carr-mobile-controls__back {
    left: 0;
}

.carr-mobile-controls__back:active, .carr-mobile-controls__back:focus {
    background: rgb(19,19,19);
    background: linear-gradient(90deg, rgba(19,19,19,0.3) 0%, rgba(0,0,0,0) 100%);
}

.carr-mobile-controls__next {
    right: 0;
}

.carr-mobile-controls__next:active, .carr-mobile-controls__next:focus {
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(19,19,19,0.3) 100%);
}

.hero-section__background-frontlight {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg,rgb(14, 23, 47, 0.80) 10%,rgb(14, 23, 47, 0.18) 88%);
    pointer-events: none;
    z-index: 1;
    animation-duration: 500ms;
}

.hero-section__background-image{
    position:absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.hero-section__content {
    width: 100%;
    max-width: var(--max-width-container-extended);
    box-sizing: border-box;
    margin: 0 auto;
    display: flex;
    align-items: flex-end;
    z-index: 2;
    height: inherit;
    padding-top: calc(5rem + 71px);
}

.hero-section__content.articlepage {
    max-width: var(--max-width-container);
    padding-bottom: 2.6rem;
}

.hero-section__header {
    width: 100%;
}
.hero-section__category {
    margin: 0.5rem 0 0 0;
    line-height: 1.2em;
    animation-duration: 600ms;
}
.hero-section__title {
    margin: 1rem 0 1rem 0;
    animation-duration: 400ms;

    line-height: var(--heading-s-line-height);
    font-size: var(--heading-s-font-size);
    letter-spacing: var(--heading-s-letter-spacing);
}

.hero-section__cta {
    margin: 0;
    animation-duration: 400ms;
}

.hero-section__row {
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 0 0 0 3px;
    line-height: 2.5rem;
}
.hero-section__date {
    margin: 0 2rem 0 0;
}
.hero-section__author {
    margin: 0;
}
.hero-section__date, .hero-section__author {
    font-size: 1.8rem;
}

.main-section {
    position:relative;
    padding-top: 2rem;
    box-sizing: border-box;
    padding-left: var(--padding-viewport);
    padding-right: var(--padding-viewport);
}

.main-section__content{
    position: relative;
    margin: 0 auto;
    max-width: var(--max-width-container);
}


.main-section__article-tags {
    /* border-bottom: 1px solid var(--color-line); */
    width: 100%;
    padding: 2.5rem 0;
    margin-bottom: 2rem;
    font-size: 1.6rem;
}

.main-section__article-tags ul {
    display: inline-flex;
    flex-wrap: wrap;
    list-style: none;
    padding:0px;
    margin: 0px;
}

.main-section__article-tags ul li {
    margin-right: 2rem;
}

.main-section__article-tags a {
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
}

.main-section__aside {
    margin-top: 2.4rem;
    display:none;
}

.responsive-picture {
    object-fit: cover;
    object-position: center center;
    height: 100%;
    width: 100%;
}

.header {
    position: relative;
    width: 100%;
    top:0;
    background: transparent;
    box-shadow: none;
    z-index: 4;
    border-bottom: 1px solid var(--color-line);
    box-sizing: border-box;
    padding-left: var(--padding-viewport);
    padding-right: var(--padding-viewport);
    background-color: var(--color-bg-2);
}

.header__content {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: 30% auto;
    grid-auto-flow: row dense;
    height: inherit;
    margin: auto;
    max-width: var(--max-width-container-extended);
    padding: 1.1rem 0;
    align-items: center;
}
.header__brand {
    display: none;
    width: 100%;
    max-width: 100px;
    min-width: 130px;
    justify-content: flex-start;
    align-items: center;
    height: inherit;
    object-fit: contain;
    object-position: center;
}

.header__brand img {
    width: 100%;
    height: 100%;
    filter: drop-shadow(1px 1px 8px rgb(0 0 0 / 12%));
}

.header .header__content .header__brand.brand-darkblue {
    display: flex;
}
.header .header__content .header__brand.brand-white {
    display: none;
}

.header-fixed {
    position: fixed;
    background: rgb(255 255 255 / 87%);
    animation-duration: 400ms;
    box-shadow: 0px 4px 16px rgb(14 11 30 / 3%), 0px 2px 3px rgb(14 11 30 / 7%);
    backdrop-filter: blur(20px);
    border: none;
}

.header-fixed .header__content .header__brand {
    max-width: 110px;
    min-width: 110px;
}

.header-transparent {
    position: absolute;
    border-bottom: none;
    background: transparent;
}

.header-transparent .header__content .header__brand.brand-darkblue {
    display: none;
}

.header-transparent .header__content .header__brand.brand-white {
    display: flex;
}

.header__menu {
    display: none;
    position: relative;
    font-weight: 600;
    height: 100%;
}
.header__menu nav {
    height: inherit;
    width: 100%;
    margin: auto;
}
.header__menu ul {
    display: inline-flex;
    width: 100%;
    height: inherit;
    justify-content: center;
    list-style: none;
    padding: 0px;
    margin: 0px;
    align-items: center;
}
.header__menu li {
    height: inherit;
    display: flex;
    align-items: center;
    margin: 0px 25px;
}
.header__menu a {
    height: 100%;
    display: flex;
    align-items: center;
    letter-spacing: 0.14em;
    font-kerning: normal;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
    transition: ease-in-out;
}
.header__menu a:hover {
    color: var(--color-blue-ac);
}
.header__cta {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.footer{
    font-size: 1.8rem;
    width: 100%;
    padding: 3rem 0 2rem;
    background-color: var(--color-bg-2);
    box-sizing: border-box;
    padding-left: var(--padding-viewport);
    padding-right: var(--padding-viewport);
    z-index: 2;
}
.footer__content{
    margin: auto;
    max-width: var(--max-width-container-extended);
}

.footer__menu, .footer__menu-subitem {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
}
.footer__menu-item {
    margin-bottom: 39px;
    display: flex;
    flex-direction: column;
}
.footer__menu-item-header {
    display: block;
    text-decoration: none;
    padding-bottom: 6px;
    margin-bottom: 1.2em;
    border-bottom: 1px solid var(--color-line);
    color: var(--color-text);
    font-weight: 600;
}
.footer__menu-subitem li a {
    display: block;
    margin-bottom: 0.5em;
    text-decoration: none;
    color: var(--color-text);
    font-weight: 600;
    line-height: 1.7em;
    transition: all .2s cubic-bezier(1, 0, 0, 1);
}
.footer__menu-subitem li a:hover {
    color: var(--color-blue-ac);
}

.footer__brand{
    width: 100%;
    padding: 1rem 0;
}
.footer__brand p{
    margin:0;
}

.footer__brand .footer__brand-logo {
    max-width: 210px;
    width: 100%;
}

.footer__brand .footer__brand-logo picture img {
    width: 100%;
    height: 100%;
}

.breadcrumb__container {
    padding: 0 var(--padding-viewport);
    background-color: white;
    margin-top: 3rem;
}

ul.breadcrumb__content {
    font-family: var(--font-family-heading);
    max-width: var(--max-width-container-extended);
    list-style: none;
    margin: 4px auto;
    padding-left: 0.5rem;
    font-weight: 600;
    letter-spacing: -0.05rem;
    font-stretch: expanded;
}

ul.breadcrumb__content li {
    display: inline;
    font-size: 1.7rem;
}

ul.breadcrumb__content li+li:before {
    padding: 8px;
    color: black;
    content: "/\00a0";
}
ul.breadcrumb__content li a {
    color: var(--color-blue-ac);
    text-decoration: none;
}

ul.breadcrumb__content li a:hover {
    color: var(--color-blue-ac);
    text-decoration: underline;
}

.related-posts {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    min-height: 400px;
    z-index:2;
}

.related-posts.margin-top {
    margin-top: 50px;
}

.related-posts__content {
    position: relative;
    margin: 0 auto;
}

.related-posts__header {
    width: 100%;
    font-size: 200%;
    margin: 0;
    margin-bottom: 3.2rem;
}

.related-posts__header.left {
    text-align: left;
    margin-left: 0.5rem;
}
.related-posts__header.center {
    text-align: center;
}

.related-posts.darkblue {
    background-color:var(--color-blue-ac-dark);
}

.related-posts.darkblue .related-posts__content .related-posts__header {
    color: white;
}

.related-posts.white {
    background-color:white;
    padding: 7rem var(--padding-viewport);
}

.related-posts.white .related-posts__content .related-posts__header {
    color: var(--color-text);
}

.related-posts.related-posts-tags {
    padding: 0rem var(--padding-viewport);
    padding-bottom: 4rem;
}

.related-posts__grid {
    display: flex;
    flex-wrap: wrap;
    flex: 0 0 auto;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-left: -5px;
    margin-right: -5px;
}

.related-card {
    flex: 33.3%;
    padding: 0;
    margin: 0;
    min-width: 300px;
}

.flex-100 {
    flex:100%;
}

.related-card__body {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: inherit;
    border-radius: 7px;
    overflow: hidden;
    margin: 10px;
    text-decoration: none;
    color: var(--color-text);
}

.related-card__media-wrapper {
    position: relative;
    width: inherit;
    flex: 1 1 auto;
    height: 25vw;
    min-height: 200px;
    max-height: 380px;
}
.related-card__media-wrapper img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    object-fit: cover;
    object-position: left;
}

.related-card__content {
    position: relative;
    display: flex;
    width: inherit;
    flex-direction: column;
    padding: 1.6rem 1.8rem;
    flex: 0 0 auto;
    background: var(--color-bg-2);
}

.related-card__content h2 {
    font-family: var(--font-family-heading);
    font-size: 108%;
    margin-bottom: 1.3rem;
}

.related-card__content p {
    font-size: 1.55rem;
    margin: 0rem 0 0.5rem 0;
    line-height: 1.7em;
}

.related-card__readmore{
    font-family: var(--font-family-heading);
    font-weight: 600;
    color: var(--color-blue-ac);
    font-size: 1.5rem;
}

.related-card__category {
    font-size: 1.4rem;
    text-decoration: none;
    font-weight: 600;
    margin-bottom: 1.5rem;
    line-height: 1em;
    font-family: var(--font-family-heading);
}

.related-posts.white 
.related-posts__grid 
.related-card 
.related-card__body 
.related-card__content {
    padding: 1.6rem 0;
    background-color: white;
}

.related-posts.white 
.related-posts__grid 
.related-card 
.related-card__body 
.related-card__media-wrapper {
    border-radius: 7px;
    overflow: hidden;
}

.related-posts__morebtn {
    margin: 2rem auto;
}

.hero-section-home-carr {
    position: relative;
    min-height: 70vh;
    background-color: #f1f1f1;
}

.hero-section-home-carr__cards-container {
    display:flex;
    width: 100%;
    height: var(--hero-cards-height);
    padding-left: calc(var(--padding-viewport) * -1);
    padding-right:calc(var(--padding-viewport) * -1);
    z-index: 4;
}

.hero-section-home-carr__cards-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: inherit;
    max-width: var(--max-width-container-extended);
    box-sizing: border-box;
    margin: 0 auto;
}



.carr-cards__grid {
    display: grid;
    column-gap: 2vw;
    grid-template-columns: 1fr 1fr 1fr;
    margin:0;
    padding: 0;
    list-style: none;
    width: 100%;
    height: inherit;
    justify-content: space-between;
}

.carr-cards__grid .carr-cards__card {
    position:relative;
    padding: 2.5rem 0rem 2rem 0rem;
    height: inherit;
    animation-duration: 600ms;
}

/* .carr-cards__grid .carr-cards__card:nth-last-child(1) {
    padding-right: 0;
}

.carr-cards__grid .carr-cards__card:nth-child(1) {
    padding-left: 0;
} */

.carr-cards__grid .carr-cards__card .carr-cards__card-body {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.65);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    transition: all 300ms ease-in-out 0ms;
}

.carr-cards__grid .carr-cards__card .carr-cards__card-body.active {
    color: white;
    transition: all 300ms ease-in-out 0ms;
}

.carr-cards__grid .carr-cards__card .carr-cards__card-body .carr-cards__category {
    flex:10%;
    font-size: 1.5rem;
}

.carr-cards__grid .carr-cards__card .carr-cards__card-body .carr-cards__title {
    overflow: hidden;
    text-overflow: ellipsis;
    flex:90%;
    font-size: 1.7rem;
    margin:0;
}

.carr-cards__timeout-container {
    width: 100%;
    display: grid;
    justify-content: space-between;
    position: relative;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 2vw;
    height: 2px;
}

.carr-cards__timeout {
    top: 0;
    width: 100%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.42);
}

.carr-cards__timeout .carr-cards__timeout-color {
    width: 0;
    height: 100%;
    background-color: var(--color-red-ac);
}

.carr-cards__timeout .carr-cards__timeout-color.w-100 {
    width: 100%;
}


/*
 * Visual Studio 2015 dark style 
