@keyframes spinner-ripple {
    0% {
      top: 47.47px;
      left: 47.47px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 8.585px;
      left: 8.585px;
      width: 77.77px;
      height: 77.77px;
      opacity: 0;
    }
  }.spinner-ripple div {
    position: absolute;
    border-width: 3.0300000000000002px;
    border-style: solid;
    opacity: 1;
    border-radius: 50%;
    animation: spinner-ripple 1.1111111111111112s cubic-bezier(0,0.2,0.8,1) infinite;
  }.spinner-ripple div:nth-child(1) {
    border-color: #d81927;
    animation-delay: 0s;
  }
  .spinner-ripple div:nth-child(2) {
    border-color: #3baae2;
    animation-delay: -0.5555555555555556s;
  }
  .spinner-ripple-container {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
  }
  .spinner-ripple-container.static {
    position: static;
  }
  .spinner-ripple {
    width: 100px;
    height: 100px;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0; /* see note above */
  }
  .spinner-ripple div { box-sizing: content-box; }
