}

.blog-progress-bar {
    width: 100%;
    pointer-events: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    max-height: 3px;
    position: absolute;
    background-color: transparent;
    border: 0;
}

progress::-webkit-progress-bar {
    background-color: transparent;
}

progress::-moz-progress-bar {
    background-color: transparent;
}

progress::-webkit-progress-value { 
    background: var(--color-blue-ac);
 }
progress::-moz-progress-value {
    background: var(--color-blue-ac);
}
progress { 
    color: var(--color-blue-ac); 
}

progress[value], progress[value]::-webkit-progress-value, progress[value]::-moz-progress-bar {
    background-color: transparent;
    appearance: none;
